@import "src/styles/global.scss";
.checkbox {
  border-radius: 4px;
  transition-property: box-shadow, border-color;
  transition-duration: $effectDuration;
  cursor: pointer;

  &__input {
    display: none;
  }

  &__content {
    border: 1px solid #90a0b0;
    border-radius: 4px;
    padding: 8px 16px;
    width: -moz-max-content;
    width: max-content;
    font-family: $font-family;
    font-weight: 500;
    font-size: 14px;
    color: $color-text;
    transition: box-shadow $effectDuration;
    -webkit-user-select: none;
       -moz-user-select: none;
            user-select: none;

    &:hover {
      box-shadow: 0 0 8px 0 rgba(33, 9, 1, 0.16);
    }
  }

  &--hidden {
    display: none;
  }

  &--error &__content {
    border-color: #d4af37;
    box-shadow: 0 0 4px 0 rgba(182, 98, 51, 0.5);
  }

  &__input:checked + &__content {
    border-color: $color-primary;
    padding-right: 30px;
    background-image: url('../../assets/images/cross.svg');
    background-position: right 14px center;
    background-repeat: no-repeat;
  }
}
