@import "src/styles/global.scss";
.dropdown {
  position: relative;
  color: $color-primary;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    border: 1px solid #90a0b0;
    border-radius: 4px;
    padding: 7px 16px;
    width: 100%;
    line-height: 17px;
    background-color: $color-bg-accent;
    cursor: pointer;
  }

  &__btn-text {
    font-family: $font-family;
    font-weight: 500;
    font-size: 14px;
    color: $color-primary;
  }

  &__svg {
    transition: transform $effectDuration;

    &--active {
      transform: rotate(180deg);
    }
  }

  &__content {
    position: absolute;
    bottom: 0;
    z-index: 10;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border: 1px solid #90a0b0;
    border-top: 0;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    width: 100%;
    background-color: $color-bg-accent;
    opacity: 0;
    transform: translateY(100%);
    transition: opacity 5s;
    gap: 16px;
    padding-block: 16px 8px;
  }

  &__item {
    border: none;
    width: 100%;
    font-family: $font-family;
    font-weight: 500;
    font-size: 14px;
    text-align: left;
    color: $color-text;
    background-color: $color-bg-accent;
    cursor: pointer;
    padding-inline: 16px;
  }

  &--active &__btn {
    border-bottom: 1px solid transparent;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  &--active &__content {
    opacity: 1;
  }

  &--error &__btn {
    border-color: #d4af37;
    box-shadow: 0 0 4px 0 rgba(182, 98, 51, 0.5);
  }

  &--error &__content {
    border-color: #d4af37;
    box-shadow: 0 0 4px 0 rgba(182, 98, 51, 0.5);
  }
}
